import { BasicColumn, FormProps } from '/@/components/Table';
import { defHttp } from '/@/utils/http/axios';
import dayjs from 'dayjs';
import { useUserStore } from '/@/store/modules/user';

enum Api {
  getOrgInfoUrl = '/bi/common/org/info',

  getStationUrl = '/baseset/common/list-station',

  getSettlementUrl = '/bi/elec/settlement',

  exportExcelUrl = '/bi/elec/export-settlement',
}

//导出
export const exportExcel = (params) =>
  defHttp.post(
    { url: Api.exportExcelUrl, params, responseType: 'blob' },
    { isReturnNativeResponse: true },
  );

export const getSettlementData = (params) =>
  defHttp.get({ url: Api.getSettlementUrl, params }, { isOnlyResult: true });

export const getOrgInfoData = (params) =>
  defHttp.get({ url: Api.getOrgInfoUrl, params }, { isOnlyResult: true });

export const getStationData = (params) => {
  return defHttp.post({ url: Api.getStationUrl, params });
};

export const formatNumber = (value) => {
  value += '';
  const list = value.split('.');
  const prefix = list[0].charAt(0) === '-' ? '-' : '';
  let num = prefix ? list[0].slice(1) : list[0];
  let result = '';
  while (num.length > 3) {
    result = `,${num.slice(-3)}${result}`;
    num = num.slice(0, num.length - 3);
  }
  if (num) {
    result = num + result;
  }
  return `${prefix}${result}${list[1] ? `.${list[1]}` : ''}`;
};

export const searchFormSchema: Partial<FormProps> = {
  labelWidth: 120,
  actionColOptions: {
    span: 24,
  },
  autoAdvancedLine: 1,
  showAdvancedButton: true,
  baseColProps: {
    span: 7,
  },
  autoSubmitOnEnter: true,
  noHideBtnFlag: true,
  schemas: [
    {
      field: `yearValue`,
      label: `年份`,
      component: 'YearPicker',
      required: true,
      defaultValue: dayjs(new Date()).format('YYYY'),
      componentProps: {
        format: 'YYYY',
        valueFormat: 'YYYY',
        style: { width: '100%' },
      },
    },
    {
      field: `orgId`,
      label: `所属公司`,
      component: 'ApiSelect',
      componentProps: {
        options: [],
        api: getOrgInfoData,
        placeholder: '请选择所属公司',
      },
      show: useUserStore().isMultiOrganization,
    },
    {
      field: `stationId`,
      label: `电站名称`,
      component: 'ApiSelect',
      componentProps: {
        options: [],
        onChange: (value) => {
          console.log(value);
        },
        api: async () => {
          const sts = await getStationData({});
          const map: any = [];
          sts.result.forEach((e) => {
            map.push({ value: e.powerStationId, label: e.powerStationName });
          });

          return map;
        },
        placeholder: '请选择电站',
      },
    },
  ],
};

